// components/Sidebar/AccountSidebarPreview.js
import React from "react";
import { Stack, Divider } from "@mui/material";
import { AccountPreview } from "@toolpad/core";

export function AccountSidebarPreview(props) {
  const { handleClick, open, mini, account } = props;

  return (
    <Stack direction="column" p={0} overflow="hidden">
      <Divider />
      <AccountPreview
        variant={mini ? "condensed" : "expanded"}
        handleClick={handleClick}
        open={open}
      />
      <Stack direction="column" sx={{ padding: 2 }}>
        {/* Hiển thị tên và email */}
        <Typography variant="h6">{account.name}</Typography>
        <Typography variant="body2" color="textSecondary">
          {account.email}
        </Typography>
      </Stack>
    </Stack>
  );
}
