import React, { useState } from "react";
import { Menu, MenuItem, IconButton } from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";

function SidebarFooterAccount({ mini }) {
  const [anchorEl, setAnchorEl] = useState(null);

  // Mở menu khi click vào dấu ba chấm
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  // Đóng menu khi chọn một mục
  const handleClose = () => {
    setAnchorEl(null);
  };

  // Xử lý sự kiện cho các mục trong menu
  const handleEditProfile = () => {
    // Thực hiện hành động chỉnh sửa thông tin ở đây
    console.log("Chỉnh sửa thông tin");
    handleClose();
  };

  const handleChangePassword = () => {
    // Thực hiện hành động đổi mật khẩu ở đây
    console.log("Đổi mật khẩu");
    handleClose();
  };

  const handleLogout = () => {
    // Thực hiện hành động logout ở đây
    console.log("Đăng xuất");
    handleClose();
  };

  return (
    <div>
      {/* Dấu ba chấm để mở menu */}
      <IconButton onClick={handleClick}>
        <MoreVertIcon />
      </IconButton>

      {/* Menu */}
      <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleClose}>
        <MenuItem onClick={handleEditProfile}>Chỉnh sửa thông tin</MenuItem>
        <MenuItem onClick={handleChangePassword}>Đổi mật khẩu</MenuItem>
        <MenuItem onClick={handleLogout}>Đăng xuất</MenuItem>
      </Menu>

      {/* Hiển thị thông tin tài khoản */}
      <div>
        <p>{account.name}</p>
        <p>{account.email}</p>
      </div>
    </div>
  );
}

export default SidebarFooterAccount;
