// pages/DashboardLayoutAccountSidebar.js
import React, { useState, useMemo } from "react";
import { AppProvider } from "@toolpad/core";
import { DashboardLayout } from "@toolpad/core";
import { SidebarFooterAccount } from "../components/Sidebar/SidebarFooterAccount";
import { DemoPageContent } from "../components/DemoPageContent";
import { createTheme } from "@mui/material/styles";

const demoTheme = createTheme({
  cssVariables: { colorSchemeSelector: "data-toolpad-color-scheme" },
  colorSchemes: { light: true, dark: true },
});

export default function DashboardLayoutAccountSidebar(props) {
  const [pathname, setPathname] = useState("/dashboard");

  const router = useMemo(
    () => ({
      pathname,
      searchParams: new URLSearchParams(),
      navigate: (path) => setPathname(String(path)),
    }),
    [pathname]
  );

  return (
    <AppProvider
      navigation={[]}
      router={router}
      theme={demoTheme}
      authentication={{ signIn: () => {}, signOut: () => {} }}
      session={{ user: { name: "Huy Nguyen" } }}
    >
      <DashboardLayout
        slots={{
          toolbarAccount: () => null,
          sidebarFooter: SidebarFooterAccount,
        }}
      >
        <DemoPageContent pathname={pathname} />
      </DashboardLayout>
    </AppProvider>
  );
}
